    .nav {
        grid-area: n;
        background-color: #57CB40;
        padding-left: 10px;
        padding-top: 10px;
        max-width: 200px;
        min-height: 1000px;
    
        
    }
    .logo{
        margin: 0px;
        width: 90px;
        height: 90px;
    }
    .highBox {
        display: flex;
        flex-wrap: wrap;
        flex-shrink: 0;
        width: 100%;
        max-width: 180px;
           
    }
    
    .box {
        left: 0;
        z-index: 10;  
    }
    
    .item :any-link{
        color: black;
        text-decoration: none;
    }
    .item a.active {
        color: #054705;
    }
   
    .friends{
        border: 4px double white ;
        width: 180px;
        text-align: center;
        height: auto;
        min-height: 200px;
        top: 50px;
        margin: 0px auto;
        position: relative;
    }


    .friend img{
        width: 30px;
        height: 30px;
        border-radius: 30px;
        border: yellowgreen;

    }
    .friend :any-link  {
        display: inline-block;
        justify-items: left;
        text-align: center;
        margin-right: 5px;
        text-decoration: none;
        color: darkblue;
        font-size: x-small;
    }
    .img_div{
        float: left;
        margin: 15px auto;
    }
    .img_div p{
        font-family: 'Times New Roman';
        font-size: small;
        float: right;
    }
    .hidden {
        visibility: hidden;
    }