.main_wrapper {
    position: relative;
    margin: 10px;
    width: 650px;
    height: 50px;
}

.custom_button {
    position: relative;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    transition-duration: 0.2s;
}
.custom_button:hover {
    background-color: rgb(52, 109, 189);
}
.custom_button button {
    display: none;
    
}
.pages {
    display: flex;
    justify-content: space-between;
   width: 80%;


}
.span{
    display: flex;
    width: 50px;
    font-family: Verdana;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    padding: 8px;
    transition-duration: 0.2s;
}
.span.active {
    background-color: #35c73a ;
}
.icon {
    font-weight: 600;
    position: absolute;
    
}
.span:hover:not(.active) {
    background-color: rgba(27, 145, 118, 0.734);
}
