.box {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(90deg,#65c3c2 0%, #5077ca 80%);
    background: -webkit-linear-gradient(90deg,#65c3c2 0%, #5077ca 80%);
    background: -moz-linear-gradient(90deg,#65c3c2 0%, #5077ca 80%);
}
.form {
    display: flex;
    left: 35%;
    top: 80%;
    width: 300px;
    height: 300px;
    justify-content: center;
    align-items: center; 
    background-color: RGBA(0,72,123,0.76);
    flex: 0 0 300px;
    border-radius: 10px;
}

.formError {
    color: red;
    border: 2px solid red;
    max-width: 40%;
    min-width: relative;
    }
.form{
box-shadow: 10px 10px 38px 9px rgba(0,0,0,0.38);
-webkit-box-shadow: 10px 10px 38px 9px rgba(0,0,0,0.38);
-moz-box-shadow: 10px 10px 38px 9px rgba(0,0,0,0.38);
transition-duration: 0.3s;
}
.form:hover {
    transform: scale(1.2) translate(-1px,-2px);
}
.form form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    flex-direction: column;
}
.form form:last-child {
    flex-basis: 50px 50px;
}

 @media (max-width: 500px) {
    .form:hover {
        transform: none;
    }
}
@media (max-width: 400px) {
    .form {
        height: 200px;
        border-radius: 15px;
    }
    .form form {
        margin: 0 auto;
        flex-direction: column;
        flex-wrap: wrap;
        flex: 0 0 ;
    }
}